/* Extra-Extra Small Devices, Phones */
@media only screen and (max-width: 320px) {
  #why {
    font-size: 8px; }
  #secreturi-wrap {
    width: 70%; } }

/* Extra Small Devices, Phones */
@media only screen and (max-width: 480px) {
  body {
    padding: 0 2%;
    font-size: 10px; }
  h1 {
    font-size: 18px; }
  h2 {
    font-size: 16px; }
  .sections {
    padding: 3% 4%; }
  textarea {
    border: solid lightgrey 2px; }
  #link {
    padding: 3% 2%; }
  #header {
    padding: 15px 0 5px 0; }
  .left-icon img {
    width: 30px; }
  .cmd img {
    width: 20px; }
  .help-container {
    font-size: 16px; } }

/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
  body {
    padding: 0 4%;
    font-size: 14px; }
  #btn-copy {
    width: 100%; } }

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {
  .help-box {
    width: 80%; } }

/* Large Devices, Wide Screens */
.cf:before,
.cf:after {
  content: ".";
  display: table;
  height: 0;
  font-size: 0;
  line-height: 0;
  visibility: hidden; }

.cf:after {
  clear: both; }

/* For IE 6/7 (trigger hasLayout) */
.cf {
  *zoom: 1; }

.clearfix:after {
  content: "";
  display: table;
  clear: both; }

.float-left {
  float: left;
  display: block; }

.padding-small {
  padding: 20px 0; }

body {
  background-color: #e6e6e6;
  padding: 0 9%;
  color: #35363a; }

#wrapper {
  width: 100%;
  font-family: 'Lato', sans-serif; }

.sections {
  padding: 3% 8%;
  background-color: #fff;
  border-left: solid #d9d9d9 5px;
  border-right: solid #d9d9d9 5px; }

.section-inner {
  padding: 10px 0; }

.btn {
  cursor: pointer !important; }

#header {
  padding: 25px 0 20px 0; }

#header-top {
  font-size: 30px;
  font-weight: 900; }

#logo {
  height: auto;
  max-width: 10%; }

#header-left {
  float: left; }

#header-right {
  text-align: right; }

#sit {
  text-decoration: none;
  color: #ee7128; }

.header-link {
  text-decoration: none;
  color: #35363a; }

#header:after {
  content: "";
  display: table;
  clear: both; }

.footer {
  font-weight: 200;
  text-align: center; }

.footer a {
  text-decoration: none;
  color: #dd4a22; }

h1,
h2,
h3 {
  margin: 0 0 20px; }

h4,
h5,
h6 {
  margin: 0 0 10px; }
